function MatchHistory({history}) {

    return(
        <div>
            <table className="table">
                <thead>
                    <tr>
                        <th scope="col">Your Hand</th>
                        <th scope="col">A.I</th>
                        <th scope="col">Result</th>
                    </tr>
                </thead>
                <tbody id="scroll">
                    {history.slice().reverse().map((value, index) => (
                        <tr key={index}>
                            <td>{value.user}</td>
                            <td>{value.AI}</td>
                            <td>
                                <img className="playerImage" src={require(`../image/${value.winnerImg}.png`)} alt="Winner"/>
                                {value.result}
                            </td>
                        </tr>
                    ))}
                </tbody>
            </table>
        </div>
    );
}

export default MatchHistory;