
import { useEffect, useState } from 'react';
import Footer from './Footer';
import '../style/Main.css'
import MatchHistory from './MatchHistory';
import axios from 'axios';



function Result({handleShow}) {    

    const [score, setScore] = useState({ boy: 0, AI: 0, ties: 0 });
    const [history, setHistory] = useState([]);

    //default display image (rock)
    const [boyImage, setBoyImage] = useState(require('../image/rockImage.png'));
    const [aiImage, setaiImage] = useState(require('../image/rockImage.png'));

    const [imgResult, setImgResult] = useState(require('../image/oppa.png'));
    const [result, setResult] = useState('result');
    
    useEffect(() => {
        const scoreItem = JSON.parse(localStorage.getItem('score'));
        if (scoreItem) {
            setScore(scoreItem);
        }

    }, []);

    useEffect(() => {
         const historyItem = JSON.parse(localStorage.getItem('history'));
            if (historyItem) {
                setHistory(historyItem);
            }
    },[]);

    let aiChoice = () => {
        const choices = ["rock", "paper", "scissor"];
        const randomIndex = Math.floor(Math.random() * choices.length);

        return choices[randomIndex];
    }

    let handleOnChoice = (boyChoice, aiChoice) => {

        let result = 'Tie';
        if((boyChoice == 'rock'        && aiChoice == 'paper')
            || (boyChoice == 'paper'   && aiChoice == 'scissor')
            || (boyChoice == 'scissor' && aiChoice == 'rock')
         ) {
            result = 'A.I Won';
             
         }else if((aiChoice == 'rock' && boyChoice == 'paper')
            || (aiChoice == 'paper'   && boyChoice == 'scissor')
            || (aiChoice == 'scissor' && boyChoice == 'rock')) {
            result = 'Player Won'
         }

        let newScore = { ...score };

            if (result === 'Player Won') {
                newScore.boy += 1;
            } else if (result === 'A.I Won') {
                newScore.AI += 1;
            } else {
                newScore.ties += 1;
            }
            setScore(newScore);

        var winnerImg = 'tie';
            if(result === 'Player Won') {
                winnerImg = 'boy'
            }else if(result == 'A.I Won') {
                winnerImg = 'AI'
            }

        let obj = {
            "user":boyChoice,
            "AI": aiChoice,
            "result": result,
            "winnerImg": winnerImg,
        };

        setResult(result);
        setImgResult(require(`../image/${winnerImg}.png`));
        setBoyImage(require(`../image/${boyChoice}Image.png`));
        setaiImage(require(`../image/${aiChoice}Image.png`));

        setHistory(history => [...history, obj]);

        const scoreSTR = JSON.stringify(newScore);
        localStorage.setItem('score', scoreSTR);
        const scoreSaved = localStorage.getItem('score');


        const historySTR = JSON.stringify(history);
        localStorage.setItem('history', historySTR);
        const historySaved = localStorage.getItem('history');
        console.log(historySaved);


    }

    function handleResetGame() {
        setScore({ boy: 0, AI: 0, ties: 0 });
        setHistory([]);

        setResult('Play Again?');
        setImgResult(require('../image/oppa.png'));

        setBoyImage(require('../image/rockImage.png'));
        setaiImage(require('../image/rockImage.png'));

        localStorage.setItem('score', JSON.stringify({ boy: 0, AI: 0, ties: 0 }));
        localStorage.setItem('history', JSON.stringify([]));

    }

    const options = {
        headers: {
            'content-type': 'application/json',
            'accept': 'application/json',
        }
    };

    useEffect(() => {
        axios.get('http://192.168.1.20:8005/api/test-api',options)
            .then((response) => {
                // setData(response.data);
                console.log(response);
            })
            .catch((error) => {
                console.error("There was an error fetching the data!", error);
            });
    }, []);

    return(
    <div className="container mainContainer">
        <h1 className="text-center">ROCK PAPER SCISSORS</h1>
        <div className="text-center">
            <button className="btn btn-secondary text-center" onClick={handleShow}>Game Rules</button>
        </div>
        <div className='resultContainer mt-5 text-center row'>
            <div className='col-4'>
                <img className='playerImage' src={require('../image/boy.png')} />
                <hr/>
                <p>Player</p>
                <p id="boy">{score.boy}</p>
            </div>
            <div className='col-4'>
                <div>
                    <img className='playerImage' src={require('../image/tie.png')} />
                    <hr/>
                    <p>Tie</p>
                    <p>{score.ties}</p>
                </div>
            </div>
            <div className='col-4'>
                <img className='playerImage' src={require('../image/AI.png')} />
                <hr/>
                <p>A.I</p>
                <p id='AI'>{score.AI}</p>
            </div>
        </div>
        <div className="row btnSelection">
            <div className="btn-group" role="group" aria-label="Basic example">
                <button type="button" className="btn btn-secondary"  onClick={() => handleOnChoice('rock', aiChoice())}>Rock</button>
                <button type="button" className="btn btn-secondary"  onClick={() => handleOnChoice('paper', aiChoice())}>Paper</button>
                <button type="button" className="btn btn-secondary"  onClick={() => handleOnChoice('scissor', aiChoice())}>Scissors</button>
            </div>
            <h1 className="text-center mt-3">Pick A Hand</h1>
            <div className='row'>
                <div className='col-12 text-center'>
                    <img id='imgResult' src={imgResult} className='playerImage' />
                </div>
                <div className='col-12'>
                    <p className='text-center' id='result'>{result}</p>
                </div>
            </div>
            <div className='row resultImage text-center'>
                <div className='col-4'>
                    <img id='boyChoiceImg' className='img-fluid' src={boyImage} style={{width:"250px"}}/>
                </div>
                <div className='col-4'>
                    <img id='boyChoiceImg' className='img-fluid' src={require('../image/vs.png')} style={{width:"250px"}}/>
                </div>
                <div className='col-4'>
                    <img id='aiChoiceImg' className='img-fluid' src={aiImage} style={{width:"250px"}}/>
                </div>
            </div>

            <div className='text-center'>
                <button className='btn btn-danger' onClick={() => handleResetGame()}>Reset Score</button>
                <h2>Match History</h2>
                <table id='matchHistory' className='table'>
                </table>
                < MatchHistory history={history} />
            </div>
        </div>
        <Footer />
    </div>
    );
}

export default Result;